.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0,0,0,.56);
}

.modal {
    top: 50%;
    left: 50%;
    position: relative;
    padding: 38px 0 40px 0;
    width: 380px;
    height: 400px;
    transform: translate(-50%, -50%);
    background-color: #f2f2f2;
    &__close {
        position: absolute;
        right: 0;
        transform: translate(50px, -45px);
    }
    &__subtitle {
        color: $black-color;
        font-family: $main-font;
        font-size: 19px;
        font-weight: 400;
    }
}

.feed-form {
    margin-top: 36px;
    display: flex;
    flex-direction: column;
    width: 300px;
    input {
        height: 50px;
        border-radius: 5px;
        background-color: #ffffff;
        margin-bottom: 15px;
        border: none;
        padding: 0 20px;
        color: #4b4b4b;
        font-size: 14px;
        font-weight: 300;
        &:nth-child(3) {
            margin-bottom: 20px;
        }
    }
    &_mt25 {
        margin-top: 25px;
    }
}

